import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";
import Wallpaper from "../map_utils/Wallpaper";
import IconsCommon from "../map_utils/IconsCommon";

const MapLayout = ({ children }) => {
  return (
    <TransformWrapper defaultScale={1} doubleClick={{ disabled: true }}>
      <TransformComponent>
        <svg
          style={{
            height: "100dvh",
            width: "100dvw",
          }}
          xmlns={"http://www.w3.org/2000/svg"}
          viewBox={"0 0 924.69 396.16"}
        >
          <Wallpaper />
          {children}
          <IconsCommon />
        </svg>
      </TransformComponent>
    </TransformWrapper>
  );
};

export default MapLayout;
